:root {
    --footer-links-background: rgba(255, 255, 255, 0.3);
    --footer-text: #636b6f;
    --footer-background: #ecf0f5;
    --footer-logo-white: none;
    --footer-logo-blue: unset;
}

.main-footer {
    border: none;
    background-color: var(--footer-background);
    color: var(--footer-text);
}

footer {
    padding-top: 40px;
    padding-bottom: 30px;

    .grid {
        max-width: 1140px;
        margin: 0 auto;

        .cell {
            h5 {
                font-weight: 500;
                padding: 0.5rem 0;
            }

            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            text-align: center;
            margin-bottom: 0.5rem;
        }
    }

    img.logo-white {
        display: var(--footer-logo-white);
    }

    img.logo-blue {
        display: var(--footer-logo-blue);
    }

    ul {
        list-style-type: none;
        margin: 0;
        padding: 0;

        li {
            padding: 0.3rem 0;
            font-weight: 400;
            font-size: 0.8rem;
            line-height: 1.3;

            a {
                word-break: break-all;

                &:hover {
                    transition: all 0.2s ease;
                    text-decoration: none;
                }
            }
        }
    }

    .kanka-logo {
        font-size: 1rem;
        font-weight: 700;

        a {
            padding: 5px;
            display: inline-block;
        }
    }

    a {
        display: inline-block;
        border-radius: 4px;
        letter-spacing: 1.1px;
    }

    .language-switcher {
        display: block;
    }

    .footer-socials {
        padding: 1rem 0;

        .email, .socials, .language-switcher {
            display: block;
            margin-bottom: 10px;

            a {
                font-size: 1.25rem;
                margin-right: 8px;
            }
        }
    }

    .footer-copyright {
        font-size: 0.7rem;
    }

    .footer-links {
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
    }
}

#language-select-modal {
    li > a {
        display: block;
    }
}

@media (min-width: 768px) {
    footer {
        .footer-socials {
            .email, .socials, .language-switcher {
                display: inline;
            }

            .email, .socials {
                margin-right: 20px;
            }
        }

        .grid > .cell {
            text-align: left;
        }

        .footer-links {
            background-color: var(--footer-links-background);
            padding: 15px;
            padding-top: 30px;
            border-radius: 15px;
        }
    }
}
